import React, { useState, useEffect } from "react";
import { navigate } from "gatsby";
import { FiArrowRight } from "react-icons/fi";
import { supabase } from "../../database/supabaseClient";
import { handleLogin } from "../../database/functions";
import { repairSupabaseKeysIfBroken, saveValidSupabaseKeys } from "../../database/session-repair";

interface LoginProps {
  route?: Record<
    string,
    {
      onTrue?: string;
      onFalse?: string;
    }
  >;
}

const Login: React.FC<LoginProps> = ({ route }) => {
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    remember: false,
  });

  const [message, setMessage] = useState("");

  /* ---------------------------------
     AUTO REDIRECT IF LOGGED IN
  ---------------------------------- */
  useEffect(() => {
    supabase.auth.getSession().then(({ data }) => {
      const role = data.session?.user?.user_metadata?.role;
      if (data.session) handleRedirect(role);
    });
  }, []);

  const handleRedirect = (role?: string | null) => {
    if (role && route?.[role]?.onTrue) return navigate(route[role].onTrue!);
    if (role && route?.[role]?.onFalse) return navigate(route[role].onFalse!);
    navigate("/event/aid/checkout");
  };

  const clearStaleSupabaseKeys = () => {
    Object.keys(localStorage).forEach((key) => {
      if (key.startsWith("sb-")) localStorage.removeItem(key);
    });
  };

  const onSuccess = async () => {
    const { data } = await supabase.auth.getSession();
    saveValidSupabaseKeys();
    const role = data.session?.user?.user_metadata?.role;
    setMessage("Login successful");
    setTimeout(() => handleRedirect(role), 900);
  };

  const onFail = (err?: string) => {
    setMessage(err || "Invalid credentials");
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, type, checked } = e.target;
    setFormData((p) => ({ ...p, [name]: type === "checkbox" ? checked : value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    repairSupabaseKeysIfBroken();
    clearStaleSupabaseKeys();
    handleLogin({
      email: formData.email,
      password: formData.password,
      onSuccess,
      onFail,
    });
  };

  const styles = authStyle
  /* ---------------------------------
     APPLEâ€‘STYLE CLEAN UI
  ---------------------------------- */
  return (
    <div style={styles.page}>
      <div style={styles.card}>
        <div style={styles.header}>
          <h1 style={styles.logo}>AID</h1>
          <p style={styles.tagline}>A.I. Director</p>
        </div>

        <form onSubmit={handleSubmit} style={styles.form}>
          <div style={styles.field}>
            <label style={styles.label}>Email</label>
            <input
              style={styles.input}
              type="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              required
            />
          </div>

          <div style={styles.field}>
            <label style={styles.label}>Password</label>
            <input
              style={styles.input}
              type="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              required
            />
          </div>

          <div style={styles.row}>
            <label style={styles.checkbox}>
              <input
                type="checkbox"
                name="remember"
                checked={formData.remember}
                onChange={handleChange}
              />
              Remember me
            </label>
            <a href="/reset-password" style={styles.link}>Forgot?</a>
          </div>

          <button type="submit" style={styles.button}>
            Sign In <FiArrowRight />
          </button>

          {message && <div style={styles.message}>{message}</div>}
        </form>

        <div style={styles.footer}>
          Donâ€™t have an account? <a href="/sign-up" style={styles.link}>Create one</a>
        </div>
      </div>
    </div>
  );
};

export default Login;

